import {api} from '/js/api'
import {Languages} from "/js/util";

api({
	'getMeetingList!POST': 'activity/QueryActivitysByType', // informationType [类型 0为会议，1为活动]
	'getActivityList!POST': 'activity/QueryActivitysByType', // informationType [类型 0为会议，1为活动]
	getSub: 'activity/GetActivityContentsByActivityIdFront'
});


let divActivity = document.getElementById('divActivity');
let divConference = document.getElementById('divConference');

let render = template.compile(document.getElementById('tpl-activity').innerHTML);
let renderSub = template.compile(document.getElementById('tpl-sub').innerHTML);


api.getMeetingList({informationType: 0, pageInde: 0, pageSize: 1000}, data => {
	data.results.typeCode = 0;
	divConference.innerHTML = render(data.results);
});


api.getActivityList({informationType: 1, pageInde: 0, pageSize: 1000}, data => {
	data.results.typeCode = 1;
	divActivity.innerHTML = render(data.results);
});


/*$('#whole').on('click', '.bTitle', function () {
	window['getSub'](this);
});*/


window['getSub'] = function (id , typeCode){

	let cont = $(`#bCont_${id}`);
	if(!cont.html()){
		api.getSub({id}, data => {
			data.lng = Languages.package;
			data.typeCode = typeCode;
			cont.html(renderSub(data));
		});

		cont.on('click' , '.cont' , function () {
			location.href = './detail.html' + this.getAttribute('data-search');
		})
	}

};

